import React from 'react';
import VideoListItem from './video_list_item'

//Maps through this.state.videos with the prop of videos and then creates a new array with the video parameter.
// onVideoSelect is being passed in to VideoListItem so that onclick the selected video changes.
//A key is always needed.
const VideoList = ({videos,onVideoSelect}) => {
	const VideoItems = videos.map((video) => { return (
		<VideoListItem 
    	onVideoSelect={onVideoSelect}
    	key={video.etag} 
    	video={video}  />) 
		}
	)
	return (<ul className="col-md-4 list-group">
				{VideoItems}
			</ul>)
}

export default VideoList;

