import React, {Component} from 'react';

//When input field shows data , it's going to pass in the event.target.value to the onSearchTermChange props, as well as the state in searchbar.
class SearchBar extends Component {
	constructor(props){
		super(props)
		this.state = {term : ''}  
	}
	render() {
		return (
			 <div className="search-bar">
			 	<input value = {this.state.term} onChange = {event => this.onInputChange(event.target.value)} />
			 </div>
			)
	}
	onInputChange(term) {
		this.setState({term})
		this.props.onSearchTermChange(term);
	}
}

export default SearchBar;



